// Error Messages
const OK = "ok";
const ALREADY_FOUND = "Already found"
const NOT_IN_DICTIONARY = "Not in word list";
const TOO_SHORT = "Too short";
const MISSING_CENTER = <>Missing&nbsp;center&nbsp;letter</>;
const BAD_LETTERS = "Bad letters";

// https://docs.djangoproject.com/en/dev/ref/csrf/#ajax
export function getCookie(name: string) : string {
  let cookieValue = '';
  if (document.cookie && document.cookie !== '') {
    const cookies = document.cookie.split(';');
    for (let i = 0; i < cookies.length; i++) {
      const cookie = cookies[i].trim();
      // Does this cookie string begin with the name we want?
      if (cookie.substring(0, name.length + 1) === (name + '=')) {
        cookieValue = decodeURIComponent(cookie.substring(name.length + 1));
        break;
      }
    }
  }
  return cookieValue;
}

async function standardEvaluateWord(
  word: string,
  centerLetter: string,
  outerLetters: string,
  foundWords: string[],
  nonce: string,
  level: number,
  side: string,
  preprocessWord: (w: string) => string = ((x) => x),
): Promise<string | JSX.Element> {
  const processedWord = preprocessWord(word);
  if (foundWords.includes(word)) {
    return ALREADY_FOUND;
  }
  if (processedWord.length < 4) {
    return TOO_SHORT;
  }
  if (!processedWord.includes(centerLetter)) {
    return MISSING_CENTER;
  }
  for (const c of processedWord) {
    if (!outerLetters.includes(c) && c !== centerLetter) {
      return BAD_LETTERS;
    }
  }

  // For debugging without the server
  // await new Promise((resolve) => {
  //   setTimeout(() => {resolve('')}, 500);
  // });
  // return OK;

  const data = {"word" : word, "level" : level, "side" : side, "nonce" : nonce};
  let csrftoken = getCookie('csrftoken');
  const response = await fetch("/puzzle/so-you-think-you-can-count/check_word",
    {
      method: "POST",
      body: JSON.stringify(data),
      headers: { "X-CSRFToken": csrftoken },
    });
  const response_data = await response.json();

  if (!response_data['valid']) {
    return NOT_IN_DICTIONARY;
  } else {
    return OK;
  }
}

export class Level {
  outerLetters: string;
  centerLetter: string;
  ranks: [number, string][];
  additionalData: string[];

  constructor(
    outerLetters: string,
    centerLetter: string,
    queenBeeScore: number,
    additionalData: string[] = [],
  ) {
    this.outerLetters = outerLetters;
    this.centerLetter = centerLetter;
    this.additionalData = additionalData;
    const q = queenBeeScore;
    // for debugging
    // const q = 5;
    this.ranks = [
      [Math.round(0    * q), 'Beginner'],
      [Math.round(0.02 * q), 'Good Start'],
      [Math.round(0.05 * q), 'Moving Up'],
      [Math.round(0.08 * q), 'Good'],
      [Math.round(0.15 * q), 'Solid'],
      [Math.round(0.25 * q), 'Nice'],
      [Math.round(0.4  * q), 'Great'],
      [Math.round(0.5  * q), 'Amazing'],
      [Math.round(0.7  * q), 'Genius'],
    ];
  }

  async evaluateWord(
      word: string,
      foundWords: string[],
      nonce: string,
      level: number,
      side: string): Promise<string | JSX.Element> {
    return standardEvaluateWord(
      word, this.centerLetter, this.outerLetters, foundWords, nonce, level, side);
  }

  getScore(word: string): number {
    if (word.length < 4) return 0;
    if (word.length === 4) return 1;
    if (this.isPangram(word)) return 7 + word.length;
    return word.length;
  }

  isPangram(word: string): boolean {
    let distinctLetters: {[k: string]: boolean} = {};
    for (let i = 0; i < word.length; i++) {
      distinctLetters[word[i]] = true;
    }
    return Object.keys(distinctLetters).length >= 7;
  }

  reset() {
  }

  shouldSort(): boolean {
    return true;
  }

  getModalMessage(): string | JSX.Element {
    return '';
  }
}

class StandardLevel extends Level {
  evaluateWord(
      word: string,
      foundWords: string[],
      nonce: string,
      level: number,
      side: string): Promise<string | JSX.Element> {
    return standardEvaluateWord(
      word, this.centerLetter, this.outerLetters, foundWords, nonce, level, side);
  }
}

class ThirdLevel extends Level {
  evaluateWord(
      word: string,
      foundWords: string[],
      nonce: string,
      level: number,
      side: string): Promise<string | JSX.Element> {
    return standardEvaluateWord(
      word, this.centerLetter, this.outerLetters, foundWords, nonce, level, side,
      (w) => {
        let o = "";
        for (let i = 0; i < w.length; i++) {
          if ("bcdfghjklmnpqrstvwxyz".indexOf(w[i]) > -1) {
            o += w[i];
          }
        }
        return o;
      }
    );
  }

  isPangram(w: string): boolean {
    let o = "";
    for (let i = 0; i < w.length; i++) {
      if ("bcdfghjklmnpqrstvwxyz".indexOf(w[i]) > -1) {
        o += w[i];
      }
    }
    let distinctLetters: {[k: string]: boolean} = {};
    for (let i = 0; i < o.length; i++) {
      distinctLetters[o[i]] = true;
    }
    return Object.keys(distinctLetters).length >= 7;
  }

  getModalMessage(): string | JSX.Element {
    return (
      <p className="sb-modal-message">
        I'd like to buy a vowel.
      </p>)
  }
}

