class SixthLevel extends Level {
  evaluateWord(
      word: string,
      foundWords: string[],
      nonce: string,
      level: number,
      side: string): Promise<string | JSX.Element> {
    return standardEvaluateWord(
      word, this.centerLetter, this.outerLetters, foundWords, nonce, level, side);
  }

  getScore(word: string): number {
    const pokemon = new Set(["aggron", "aron", "gengar", "gogoat", "rattata",
      "torterra", "ditto", "dodrio", "dratini", "nidoran", "nidorina",
      "nidorino"]);
    if (pokemon.has(word)) {
      return 400;
    } else {
      return super.getScore(word);
    }
  }

  getModalMessage(): string | JSX.Element {
    return (
      <p className="sb-modal-message">
        You've got to get them all?
      </p>)
  }
}

class SeventhLevel extends Level {
  originalOuterLetters: string;

  constructor(
    outerLetters: string,
    centerLetter: string,
    queenBeeScore: number,
  ) {
    super(outerLetters, centerLetter, queenBeeScore);
    this.originalOuterLetters = outerLetters;
  }

  async evaluateWord(
      word: string,
      foundWords: string[],
      nonce: string,
      level: number,
      side: string): Promise<string | JSX.Element> {
    const evaluationResult = await standardEvaluateWord(
      word, this.centerLetter, this.outerLetters, foundWords, nonce, level, side);
    if (evaluationResult !== "ok") {
      return evaluationResult;
    }
    const lettersToIncrement: {[c: string]: boolean} = {};
    for (let i = 0; i < 6; i++) {
      if (word.indexOf(this.outerLetters[i]) > -1) {
        lettersToIncrement[this.outerLetters[i]] = true;
      }
    }
    const incrementLetter = (c: string) => {
      if (c.charCodeAt(0) < "z".charCodeAt(0)) {
        return String.fromCharCode(c.charCodeAt(0) + 1);
      } else {
        return 'a';
      }
    }

    // only one copy of a duplicate will increment
    for (let c in lettersToIncrement) {
      let i = this.outerLetters.indexOf(c);
      this.outerLetters = this.outerLetters.slice(0, i) + incrementLetter(c) + this.outerLetters.slice(i + 1);
    }

    // if we want both copies of a duplicate to increment that'd also be easy to
    // implement, just comment out 3 lines above and write here

    return evaluationResult;
  }

  isPangram(w: string): boolean {
    for (let i = 0; i < this.outerLetters.length; i++){
      if (w.indexOf(this.outerLetters[i]) === -1) {
        return false;
      }
    }
    return w.indexOf(this.centerLetter) > -1;
  }

  reset() {
    this.outerLetters = this.originalOuterLetters;
  }

  shouldSort(): boolean {
    return false;
  }
}

class FifthLevel extends Level {
  originalOuterLetters: string;
  originalCenterLetter: string

  constructor(
    outerLetters: string,
    centerLetter: string,
    queenBeeScore: number,
  ) {
    super(outerLetters, centerLetter, queenBeeScore);
    this.originalOuterLetters = outerLetters;
    this.originalCenterLetter = centerLetter;
  }

  async evaluateWord(
      word: string,
      foundWords: string[],
      nonce: string,
      level: number,
      side: string): Promise<string | JSX.Element> {
    const evaluationResult = await standardEvaluateWord(
      word, this.centerLetter, this.outerLetters, foundWords, nonce, level, side);
    if (evaluationResult !== "ok") {
      return evaluationResult;
    }
    const lastLetter = word[word.length - 1];
    if (lastLetter !== this.centerLetter) {
      this.outerLetters = this.outerLetters.replace(lastLetter, this.centerLetter);
      this.centerLetter = lastLetter;
    }
    return evaluationResult;
  }

  reset() {
    this.outerLetters = this.originalOuterLetters;
    this.centerLetter = this.originalCenterLetter;
  }
}

